* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  .footer {
    background-color: rgb(4, 4, 4);
    color: #fefefe;
    position: relative;
    width: 100%;
    bottom: 0;
    left: 0;
    padding-bottom: 1px;
    text-align: center;
    margin-left: auto;
    margin-right: auto;
    
  }
  
  .footer .heading {
    color: #fefefe;
    max-width: 60px;
    width: 100%;
    text-transform: uppercase;
    margin: 0 auto;
    margin-bottom: 3rem;
    font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  }

  .footer .content {
    display: flex;
    justify-content: space-evenly;
    /* margin: 1.5rem; */
  }
  
  .footer .content p {
    margin-bottom: 0.5rem;
  }
  
  .footer .content a {
    text-decoration: none;
    color: #fefefe;
  }
  
  .footer .content a:hover {
    border-bottom: 1px solid #ffffff;
  }
  
  .footer .content h4 {
    margin-bottom: 1.3rem;
    font-size: 19px;
    font-weight: 800;
    color: cornsilk;
  }
  
  footer {
    text-align: center;
    margin-bottom: 2rem;
  }
  
  footer hr {
    margin: 2rem 0;
  }
  
  @media (max-width: 767px) {
    .footer .content {
      display: flex;
      flex-direction: column;
      font-size: 14px;
    }
  
    .footer {
      position: unset;
    }
  }
  
  @media (min-width: 768px) and (max-width: 1024px) {
    .footer .content,
    .footer {
      font-size: 14px;
    }
  }
  
  @media (orientation: landscape) and (max-height: 500px) {
    .footer {
      position: unset;
      .footer .heading {
        color: #fefefe;
        max-width: 1010px;
        width: 90%;
        text-transform: uppercase;
        margin: 0 auto;
        margin-bottom: 3rem;
        font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
      }
      
      .footer .content {
        display: flex;
        justify-content: space-evenly;
        margin: 1.5rem;
        text-align: center;
      }
      
      .footer .content p {
        margin-bottom: 1.3rem;
        text-align: center;
      }
      
      .footer .content a {
        text-decoration: none;
        color: #fefefe;
        text-align: center;
      }
    }
  }